.App {
  height: 100vh;
  width: 100vw;
  border: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  font-family: "Roboto", sans-serif;
  letter-spacing: 1px;
  font-weight: 300;
}

.logo {
  position: absolute;
  height: 175px;
  padding: 20px;
  top: 0;
  left: 2%;
  z-index: 1;
}

section {
  position: absolute;
  color: white;
  top: 40%;
  left: 5%;
  z-index: 1;
}

li {
  padding: 20px 0;
  cursor: pointer;
}

p span {
  font-size: 35px;
}

.container p {
  position: relative;
}

.switch {
  display: inline-block;
  height: 34px;
  position: relative;
  width: 60px;
}

ul {
  position: absolute;
  bottom: 7%;
  left: 5%;
  color: white;
  line-height: 5px;
}

#countryName {
  position: absolute;
  bottom: calc(50% + 220px);
  left: calc(50% + 220px);
  color: white;
  text-transform: uppercase;
}
