.switch-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;

  p {
    padding-left: 2rem;
  }
}

.switch input {
  display: none;

  &:checked + .slider {
    background-image: linear-gradient(
      -90deg,
      rgba(255, 0, 180, 0.5),
      rgba(0, 255, 204, 0.5),
      rgba(130, 180, 220, 0.5)
    );
  }

  &:checked + .slider:before {
    transform: translateX(26px);
  }  
}

.slider {
  background-color: #ccc;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.4s;

  &::before {
    background-color: #fff;
    bottom: 4px;
    content: "";
    height: 26px;
    left: 4px;
    position: absolute;
    transition: 0.4s;
    width: 26px;
  }

  &.round {
    border-radius: 34px;
  }
  
  &.round:before {
    border-radius: 50%;
  }  
}